<template>
  <div class="cards page-back mx-auto">
    <h2 class="bold-x-lg my-5" style="font-family: KB_C3;">Î™©Ìëú Í¥ÄÎ¶¨</h2>
    
    <div class="menu-and-filters">

      <!-- Í≤ÄÏÉâ ÌïÑÌÑ∞ -->
      <div class="search-filters">
        <input type="date" v-model="searchStartDate" @change="filterTargets" placeholder="ÏãúÏûë ÎÇ†Ïßú">
        <input type="date" v-model="searchEndDate" @change="filterTargets" placeholder="Ï¢ÖÎ£å ÎÇ†Ïßú">
        <select v-model="searchStatus" @change="filterTargets">
          <option value="">Î™®Îì† ÏÉÅÌÉú</option>
          <option value="ongoing">ÏßÑÌñâ Ï§ë</option>
          <option value="scheduled">ÏòàÏ†ï</option>
          <option value="completed">Ï¢ÖÎ£å</option>
        </select>
      </div>
    </div>

    <div class="d-flex justify-content-end mr-5">
      <div class="target" @click="openModal" style="cursor:pointer"> üìù Î™©Ìëú Îì±Î°ù </div>
    </div>

    <div v-if="filteredTargets.length > 0" class="goals-list">
      <div v-for="(target, index) in filteredTargets" :key="index" class="goal-card">
        <div class="goal-info" @click="toggleExpand(index)">
          <span class="goal-date">üìÖ {{ target.start_date }} ~ {{ target.end_date }}</span>
          <span class="goal-mileage">üéØ {{ target.target_mileage }} ÎßàÏùºÎ¶¨ÏßÄ Î™©Ìëú</span>
          <span class="goal-status" :class="{ 'status-completed': isCompleted(target),
              'status-ongoing': isOngoing(target),'status-scheduled': isScheduled(target)}">
            {{ getStatusText(target) }}
          </span>
          <span class="goal-rate">‚ú®  
            {{target.totalParticipants > 0 ? Math.round((target.achievedCount / target.totalParticipants) * 100) : 0}}%  Îã¨ÏÑ±
          </span>
        </div>
        <div class="progress-container">
          <!-- <div class="progress-bar" :style="{ width: target.totalParticipants > 0 ? 
            Math.round((target.achievedCount / target.totalParticipants) * 100) + '%' : '0%' }">
          </div> -->
          <div class="progress-bar" :style="{ width: animatedWidths[index] + '%' }"></div>
        </div>
        <div v-show="expandedTargets.includes(target.target_no)" class="goal-details">
          <div style="margin-top: 40px;">
            <span style="font-size: 20px; font-family: 'KB_C2', sans-serif; margin-right: 70px;">Ï∞∏Í∞ÄÏûê {{ target.totalParticipants }} Î™Ö</span>
            <i class="bi bi-person-fill-check" style=" color: #19c99b; font-size: 27px; margin-right: 10px;"></i>
            <span style="font-size: 20px;font-family: 'KB_C2', sans-serif; color: #19c99b;">Îã¨ÏÑ± </span>
            <span style="font-size: 20px; font-family: 'KB_C2', sans-serif; color: #19c99b; margin-right: 70px;"> {{ target.achievedCount }} Î™Ö</span>
            <i class="bi bi-person-fill-x" style="color:#cf2222; font-size: 27px; margin-right: 10px;"></i>
            <span style="font-size: 20px; font-family: 'KB_C2', sans-serif; color: #cf2222;">ÎØ∏Îã¨ÏÑ± </span>
            <span style="font-size: 20px; font-family: 'KB_C2', sans-serif; color: #cf2222; margin-right: 70px;"> {{ target.notAchievedCount }} Î™Ö</span>
            <i class="bi bi-envelope-check-fill" style="color: #8c8c8c; font-size: 27px; margin-top: 10px; margin-right: 10px;"></i>
            <span v-if="isOngoing(target)" @click="generateAIContent(target, index)"  style="cursor: pointer; font-size: 18px; font-family: 'KB_C2', sans-serif; margin-right: 70px;"> Î¨∏Ïûê Î∞úÏÜ°</span>

            <!-- ÏßÑÌñâ Ï§ëÏù¥ ÏïÑÎãê Îïå "Î¨∏Ïûê Î∞úÏÜ° Î∂àÍ∞Ä" Î©îÏãúÏßÄ ÌëúÏãú -->
            <span v-else style="font-size: 18px; font-family: 'KB_C2', sans-serif; margin-right: 70px;">
              Î¨∏ÏûêÎ∞úÏÜ° Î∂àÍ∞Ä
            </span>
            <span style="font-size: 18px; font-family: 'KB_C2', sans-serif; color: #cf2222; cursor: pointer;" @click="deleteTarget(target.target_no)" >ÏÇ≠Ï†ú</span>
            <span class="loading-dots" v-if="loading[index]">{{ loadingText[index] }}</span>
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <p>Ï∞∏Í∞ÄÏûêÍ∞Ä ÏóÜÏäµÎãàÎã§.</p>
    </div>
  </div>

  <!-- Î™®Îã¨ (Í∏∞Ï°¥ ÏΩîÎìú Ïú†ÏßÄ) -->
  <div v-if="isModalOpen" class="modal-overlay">
    <div class="modal-content">
      <div class="modal-header">
        <h2 class="modal-title">{{ mile_name }} Î™©Ìëú Îì±Î°ùÌïòÍ∏∞</h2>
        <button class="close-button" @click="closeModal">&times;</button>
      </div>
      <div class="modal-body">
        <div class="form-group">
          <label>Î™©Ìëú Í∏∞Í∞Ñ</label>
          <div class="date-range">
            <Datepicker v-model="startDate" :format="formatDate" placeholder="ÏãúÏûëÏùº" :minDate="new Date()" ></Datepicker>
            <span class="date-separator">~</span>
            <Datepicker v-model="endDate" :format="formatDate" placeholder="Ï¢ÖÎ£åÏùº" :minDate="new Date()" ></Datepicker>
          </div>
        </div>
        <div class="form-group">
          <label for="mileage-score">Î™©Ìëú ÎßàÏùºÎ¶¨ÏßÄ Ï†êÏàò</label>
          <input type="number" id="mileage-score" v-model="targetScore" />
        </div>
      </div>
      <div class="modal-footer">
        <button class="submit-button" @click="addAction">Îì±Î°ù</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import Datepicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';
import api from "@/api/axios";


export default {
  name: 'TargetMileageAdminView',
  components: {
    Datepicker,
  },
  data() {
    return {
      // // currentTab: 'all',
      // totalParticipants: 0,  // Ï¥ù Ï∞∏Í∞ÄÏûê Ïàò
      // achievedCount: 0,  // Îã¨ÏÑ±Ìïú ÏÇ¨Îûå Ïàò
      // notAchievedCount: 0,  // ÎØ∏Îã¨ÏÑ±Ìïú ÏÇ¨Îûå Ïàò
      // notAchievedPhones: [],  // ÎØ∏Îã¨ÏÑ±Ìïú ÏÇ¨ÎûåÏùò Ï†ÑÌôîÎ≤àÌò∏
      // rates: [],  // Í∞Å ÌÉÄÍ≤üÏóê ÎåÄÌïú Îã¨ÏÑ±Î•† Î∞∞Ïó¥
      isModalOpen: false,
      startDate: null,
      endDate: null,
      targetScore: 0,
      mile_name: '',
      searchStartDate: '',
      searchEndDate: '',
      searchStatus: '',
      expandedTargets: [], // ÌôïÏû•Îêú Î™©Ìëú Î™©Î°ù Ï∂îÏ†Å
      loading: [],
      loadingText: [], // Î°úÎî© ÌÖçÏä§Ìä∏
      loadingInterval: [], // Î°úÎî© Ïï†ÎãàÎ©îÏù¥ÏÖò Ïù∏ÌÑ∞Î≤å
      animatedWidths: [], // Í∞Å Î™©ÌëúÏóê ÎåÄÌïú Ïï†ÎãàÎ©îÏù¥ÏÖò ÎÑàÎπÑÎ•º Ï†ÄÏû•ÌïòÎäî Î∞∞Ïó¥
    }
  },
  computed: {
    ...mapGetters('mile', ['getMileInfo', 'getArrayMiles']),
    ...mapGetters('login', ['getLoginInfo']),
    ...mapGetters('mileage', ['getTargets']),
    ...mapGetters('target', ['getParticipantsData']),

    // achievementRate() {
    // if (this.totalParticipants > 0 && !isNaN(this.achievedCount) && !isNaN(this.totalParticipants)) {
    //   return ((this.achievedCount / this.totalParticipants) * 100).toFixed(2);
    // }
    // return 0;  // Ï∞∏Í∞ÄÏûê ÏàòÍ∞Ä 0Ïù¥Í±∞ÎÇò NaNÏùº ÎïåÎäî 0%Î°ú Ï≤òÎ¶¨
    // },
    participantsData() {
        return this.getParticipantsData(this.targetNo); // ÏòàÏãúÎ°ú ÌòÑÏû¨ ÌÉÄÍ≤ü Î≤àÌò∏ ÏÇ¨Ïö©
    },
    targets() {
      return this.getTargets || [];
    },
    loginInfo(){
      return this.getLoginInfo;
    },
    mileInfo() {
      return this.getMileInfo;
    },
    formattedTargets(){
      if(!this.targets) return [];

      return this.targets.map(target => {
        const startDate = new Date(target.start_date);
        const endDate = new Date(target.end_date);
        
        return{
          ...target,
          targetRate: target.achievementRate,
          startDate,
          endDate,
          // expanded: false,
          animatedWidth: 0, // Ï¥àÍ∏∞Í∞íÏùÄ 0
        }
      });
    },
    filteredTargets() {
      let targets = this.formattedTargets;

      // ÏÉÅÌÉú Í≤ÄÏÉâ
      if (this.searchStatus) {
        targets = targets.filter(t => {
          if (this.searchStatus === 'ongoing') return this.isOngoing(t);
          if (this.searchStatus === 'scheduled') return this.isScheduled(t);
          if (this.searchStatus === 'completed') return this.isCompleted(t);
          return true;
        });
      }

       // ÎÇ†Ïßú ÌïÑÌÑ∞ÎßÅ
       if (this.searchStartDate && this.searchEndDate) {
        const startDate = new Date(this.searchStartDate);
        const endDate = new Date(this.searchEndDate);
        targets = targets.filter(t => {
          const targetStart = new Date(t.start_date);
          const targetEnd = new Date(t.end_date);
          return targetStart >= startDate && targetEnd <= endDate;
        });
      }

      return targets.sort((a, b) => new Date(a.start_date) - new Date(b.start_date));
    },
  },
  methods: {
    ...mapActions('mile', ['fetchMileInfo']),
    ...mapActions('mileage', ['addTarget', 'fetchMileTarget', 'targetDelete']),
    // ...mapActions('target', ['loadParticipants']),

    warningAlert(alret) {
      this.$swal({
        title: 'Í≤ΩÍ≥†',
        text: alret,
        icon: 'warning',
        scrollbarPadding: false,
      });
    },
    succesAlert() {
      this.$swal({
        title: "ÏÑ±Í≥µ",
        text: "Î©îÏãúÏßÄ Î∞úÏÜ°Ïù¥ ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§.",
        icon: "success",
        scrollbarPadding: false,
      });
    },
    targetAlert() {
      this.$swal({
        title: "ÏÑ±Í≥µ",
        text: "ÏÉàÎ°úÏö¥ Î™©ÌëúÍ∞Ä Îì±Î°ùÎêòÏóàÏäµÎãàÎã§.",
        icon: "success",
        scrollbarPadding: false,
      });
    },
    deleteAlert() {
      this.$swal({
        title: "ÏÑ±Í≥µ",
        text: "ÏÇ≠Ï†úÍ∞Ä ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§.",
        icon: "success",
        scrollbarPadding: false,
      });
    },
   errorAlert(message) {
    alert(`ÏóêÎü¨: ${message}`);
    },

   // startLoadingAnimation Î©îÏÑúÎìúÏóê index Ï∂îÍ∞Ä
  startLoadingAnimation(index) {
    let dots = '';
    this.loading[index] = true; // Ïù∏Îç±Ïä§Î≥ÑÎ°ú Î°úÎî© ÏãúÏûë
    this.loadingInterval[index] = setInterval(() => {
      if (dots.length < 3) {
        dots += '.';
      } else {
        dots = '';
      }
      this.loadingText[index] = 'Ï§ë' + dots; // Ïù∏Îç±Ïä§Ïóê ÎßûÍ≤å loadingText ÏóÖÎç∞Ïù¥Ìä∏
    }, 500); // 0.5Ï¥àÎßàÎã§ Ï†ê Ï∂îÍ∞Ä
  },


 // AI Î¨∏Íµ¨ ÏÉùÏÑ± Î∞è Î¨∏Ïûê Î∞úÏÜ° Í∏∞Îä•
 async generateAIContent(target, index) {

      try {
        // Î°úÎî© ÏãúÏûë
        this.startLoadingAnimation(index);

        let notAchievedNames = target.not_achieved_names || '';
        let notAchievedPhones = target.not_achieved_phones || '';

        if (typeof notAchievedNames === 'string') {
          notAchievedNames = notAchievedNames.split(',').map(name => name.trim()).filter(name => name.length > 0);
        }

        if (typeof notAchievedPhones === 'string') {
          notAchievedPhones = notAchievedPhones.split(',').map(phone => phone.trim()).filter(phone => phone.length > 0);
        }

        if (notAchievedPhones.length === 0 || notAchievedNames.length === 0) {
          this.loading[index] = false;
          clearInterval(this.loadingInterval[index]);
          this.loadingText[index] = ''; // Ïï†ÎãàÎ©îÏù¥ÏÖò ÌÖçÏä§Ìä∏ Ï¥àÍ∏∞Ìôî

          this.warningAlert('Î∞úÏÜ°Ìï† ÎåÄÏÉÅÏù¥ ÏóÜÏäµÎãàÎã§.');
          return;
        }


        // AI Î¨∏Íµ¨ ÏÉùÏÑ±
        const response = await api.post('bot/sms', null, {
          params: {
            prompt: `Ïö∞Î¶¨ ÌöåÏÇ¨ Ïù∏ÏÇ¨Í≥†Í≥ºÏôÄ Ïó∞Í≤∞ÎêòÎäî ÎßàÏùºÎ¶¨ÏßÄ Í¥ÄÎ¶¨ ÏÇ¨Ïù¥Ìä∏ÏóêÏÑú ${target.mile_name} ÎßàÏùºÎ¶¨ÏßÄÏóê Í¥ÄÌï¥ÏÑú ÌôçÎ≥¥ÏÑ± Î¨∏ÏûêÎ©îÏãúÏßÄÎ•º ÎßåÎì§Ïñ¥Ï§ò.` +
              `ÎßàÏùºÎ¶¨ÏßÄÍ∞Ä ${target.end_date}ÍπåÏßÄ Îã¨ÏÑ±ÎêòÏßÄ ÏïäÏúºÎ©¥ ÎßàÏôï Ï†êÏàòÎ•º ÌöçÎìùÌï† Ïàò ÏóÜÎã§Îäî ÏÜåÏãùÎèÑ ÏïåÎ†§Ï§ò.`+
              'ÌöåÏÇ¨ Ïù¥Î¶ÑÏùÄ Ïïà Î∞ùÌûàÏßÄ ÏïäÍ≥† 100Ïûê Ïù¥ÎÇ¥Î°ú Î≥¥ÎÇ¥Ï§ò.'+
              'ÎßàÎ¨¥Î¶¨ Î©òÌä∏Îäî ÌôúÍ∏∞Ï∞®Í≤å ÎèÑÏ†ÑÌï¥Î≥¥ÏûêÎäî ÎÇ¥Ïö©ÏúºÎ°ú !Î•º ÎÑ£ÏóàÏúºÎ©¥ Ï¢ãÍ≤†Ïñ¥' +
              'ÏùºÏÉÅÏÉùÌôú ÏÜçÏóêÏÑú Ïì∞ÏßÄ ÏïäÎäî Ïñ¥Î†§Ïö¥ Îã®Ïñ¥Îäî Ïì∞ÏßÄ ÎßêÏïÑÏ§ò'
          },
        });

        const aiMessage = response.data;

       // Î™®Îì† ÏàòÏã†ÏûêÏóêÍ≤å ÎèôÏùºÌïú AI Î©îÏãúÏßÄ Î∞úÏÜ°
    const messages = notAchievedPhones.map((phone) => {
      return {
        to: phone,
        text: aiMessage, // ÎèôÏùºÌïú AI Î¨∏Íµ¨Î•º ÏÇ¨Ïö©
      };
    });

        await api.post('/user/sendSmsAction', {
          to: messages.map(m => m.to),
          texts: messages.map(m => m.text),
          mile: target.mile_name
        });

          // Î°úÎî© Ï¢ÖÎ£å ÌõÑ Ïï†ÎãàÎ©îÏù¥ÏÖòÏùÑ Î©àÏ∂îÍ≥† ÏïåÎ¶º ÌëúÏãú
        this.loading[index] = false;
        clearInterval(this.loadingInterval[index]);
        this.loadingText[index] = ''; // Ïï†ÎãàÎ©îÏù¥ÏÖò ÌÖçÏä§Ìä∏ Ï¥àÍ∏∞Ìôî

        this.succesAlert();
      } catch (error) {
        this.loading[index] = false;
        clearInterval(this.loadingInterval[index]);
        this.loadingText[index] = ''; // Ïï†ÎãàÎ©îÏù¥ÏÖò ÌÖçÏä§Ìä∏ Ï¥àÍ∏∞Ìôî

        this.errorAlert(error.message || "Î¨∏Ïûê Î∞úÏÜ° Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
      }
    },
    animateProgressBars() {
    this.filteredTargets.forEach((target, index) => {
      const finalWidth = target.totalParticipants > 0 
        ? Math.round((target.achievedCount / target.totalParticipants) * 100)
        : 0;

      let currentWidth = 0;
      this.animatedWidths[index] = currentWidth; // Ïï†ÎãàÎ©îÏù¥ÏÖò Ï¥àÍ∏∞Ìôî

      const interval = setInterval(() => {
        if (currentWidth < finalWidth) {
          currentWidth += 1;
          this.animatedWidths[index] = currentWidth; // ÌòÑÏû¨ ÎÑàÎπÑ ÏóÖÎç∞Ïù¥Ìä∏
        } else {
          clearInterval(interval);
        }
      }, 2); // Ïï†ÎãàÎ©îÏù¥ÏÖò ÏÜçÎèÑ (Î∞ÄÎ¶¨Ï¥à)
    });
  },
  resetModalData() {
    this.startDate = null;
    this.endDate = null;
    this.targetScore = 0;
  },
    async addAction() {
             
      const targetInfo = {
        mile_no: this.loginInfo.mile_no,
        user_no: this.loginInfo.user_no,
        start_date: this.formatDate(this.startDate),
        end_date: this.formatDate(this.endDate),
        target_mileage: this.targetScore,
      }

      const response = await this.addTarget(targetInfo);

      if(response && response.data.success){
        this.targetAlert();
        this.resetModalData(); // Îç∞Ïù¥ÌÑ∞ Ï¥àÍ∏∞Ìôî
        this.closeModal(); // Î™©ÌëúÍ∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Îì±Î°ùÎêú ÌõÑ Î™®Îã¨ Ï∞ΩÏùÑ Îã´ÏäµÎãàÎã§.
        await this.refreshTargets(); // Î™©Ìëú Î™©Î°ùÏùÑ ÏÉàÎ°ú Í≥†Ïπ®
      }else{
        this.errorAlert('Î™©Ìëú Îì±Î°ùÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§', 'fail', '#');
      }
    },
    openModal() {
      this.isModalOpen = true;
    },
    closeModal() {
      this.isModalOpen = false;
    },
      showAlert(type, message, path = null) {
    this.$swal({
      icon: type,
      title: message,
      showConfirmButton: false,
      timer: 1500
    }).then(() => {
      if (path) {
        this.navigateTo(path);
      }
    });
  },
        navigateTo(path) {
      if (path) {
        this.$router.push(path);
      }
    },
    async deleteTarget(target_no) {
      console.log("target_no::",target_no);
      const response = await this.targetDelete(target_no);

      window.location.reload();
      if(response && response.data.success){
        // this.deleteAlert();
       // ÏÇ≠Ï†úÍ∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏôÑÎ£åÎêòÎ©¥ ÌéòÏù¥ÏßÄ ÏÉàÎ°úÍ≥†Ïπ®
       window.location.reload();
      }else{
        this.errorAlert('Î™©Ìëú ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§', 'fail', '#');
      }
    },
    formatDate(date) {
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },

    isOngoing(target) {
      const now = new Date();
      return target.startDate <= now && now <= target.endDate;
    },
    isScheduled(target) {
      const now = new Date();
      return target.startDate > now;
    },
    isCompleted(target) {
      const now = new Date();
      return target.endDate < now;
    },
    getStatusText(target) {
      if (this.isOngoing(target)) return 'ÏßÑÌñâ Ï§ë';
      if (this.isScheduled(target)) return 'ÏòàÏ†ï';
      if (this.isCompleted(target)) return 'Ï¢ÖÎ£å';
      return 'Ïïå Ïàò ÏóÜÏùå';
    },
    filterTargets() {
      // ÌïÑÌÑ∞ÎßÅ Î°úÏßÅÏùÄ computed ÏÜçÏÑ±ÏóêÏÑú Ï≤òÎ¶¨Îê©ÎãàÎã§.
    },
    toggleExpand(index) {
    const targetId = this.filteredTargets[index].target_no;
    const expandedIndex = this.expandedTargets.indexOf(targetId);

    if (expandedIndex === -1) {
      this.expandedTargets = [targetId];  // Îã§Î•∏ Î™©ÌëúÎäî Ï∂ïÏÜåÌïòÍ≥†, ÏÑ†ÌÉùÎêú Î™©ÌëúÎßå ÌôïÏû•
    } else {
      this.expandedTargets.splice(expandedIndex, 1);  // ÏÑ†ÌÉùÎêú Î™©ÌëúÎ•º Ï∂ïÏÜå
    }
  },
  async refreshTargets() {
    const mile_no = this.loginInfo ? this.loginInfo.mile_no : null;
    if (mile_no) {
      try {
        await this.fetchMileTarget(mile_no); // ÏÑúÎ≤ÑÏóêÏÑú ÏµúÏã† Î™©Ìëú Î™©Î°ùÏùÑ Î∂àÎü¨Ïò¥
        this.animateProgressBars(); // ÏÉàÎ°úÍ≥†Ïπ® ÌõÑ Ïï†ÎãàÎ©îÏù¥ÏÖò Í∞±Ïã†
      } catch (error) {
        console.error('Î™©Ìëú Î™©Î°ùÏùÑ Î∂àÎü¨Ïò§Îäî Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§:', error);
      }
    }
  },
  

  },
  async created(){
    const user_no = this.loginInfo ? this.loginInfo.user_no : null;
    if(user_no){
      await this.fetchMileInfo(user_no);
      const mileInfo = this.getMileInfo;
      if(mileInfo){
        this.mile_name = mileInfo.mile_no;
      }else{
        console.error('ÎßàÏùºÎ¶¨ÏßÄ Ïù¥Î¶ÑÏùÑ Í∞ÄÏ†∏Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§.');
      }
    }else{
      console.error('user_noÏù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§.');
    }

    const mile_no = this.loginInfo ? this.loginInfo.mile_no : null;
    if (mile_no) {
      try {
        await this.fetchMileTarget(mile_no);
        this.animateProgressBars(); // ÌéòÏù¥ÏßÄ Î°úÎî© ÌõÑ Ïï†ÎãàÎ©îÏù¥ÏÖò ÏãúÏûë
      } catch (error) {
        console.error('ÎßàÏùºÎ¶¨ÏßÄ Î™©Ìëú Î¶¨Ïä§Ìä∏Î•º Í∞ÄÏ†∏Ïò§Îäî Ï§ë Ïò§Î•ò Î∞úÏÉù:', error);
      }
    } else {
      console.error('mile_noÍ∞Ä Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§.');
    }
  },

};
